import React from 'react'
import destinationsData from '../Assets/destinationData';

const Visit = ({match}) => {
 const destination_place=destinationsData.find((x)=>x.city === match.params.name);
 
  return (
    <div className="visit__page">

      <div>
      <img src={destination_place.img} alt=""/>
      </div>
      
      <div className="visit__texts">
      <h1>{destination_place.city}</h1>
      <h3>{destination_place.description}</h3>
      </div>
     
    </div>
  )
}

export default Visit
