import React from 'react'
import data from '../Assets/data'

const TopPicks = () => {
 const [items,setItems]=React.useState([]);
 const handleSubmit=(str)=>{
   const renderData= data.filter((item)=>{
    return item.category===str;
   })
   setItems(renderData);
   console.log(renderData);
 }
  return (
    <div className="picks">
      <div className="picks__nav">
       <div className="picks__heading">
        <h1 >Top Picks</h1>
       </div>
       <div className="picks__filter">
        <div className="food">
         <button id="btn" onClick={()=>handleSubmit("food")}>Food</button>
        </div>
        <div >
         <button id="btn" onClick={()=>handleSubmit("places")}>places</button>
        </div>
        <div className="malls">
         <button id="btn" onClick={()=>handleSubmit("Malls")}>Malls</button>
        </div>
       </div>
      </div>

      <div>
       {
        items.map((item)=>{
         return(
           <div className="filtered_item">
          <div>
           <img id="filtered_img" src={item.image} alt=""/>
          </div>
          <div className="filtered_text">
           <h3>{item.title}</h3>
           <h4>{item.description}</h4>
          </div>
         </div>
         )
        
        })
       }
      </div>
      
    </div>
  )
}

export default TopPicks
