
import './App.css';
import Destinations from './components/Destinations';
import ShowCase from './components/ShowCase';
import TopPicks from './components/TopPicks';
import {BrowserRouter as Router,Route} from 'react-router-dom';
import Visit from './components/Visit';
import Contact from './components/Contact';
import Footer from './components/Footer';
function App() {
  return (
    <div className="App">
      <Router>       
        <Route path="https://yaswanth3100.github.io/tour-app/" exact component={ ShowCase } ></Route>
        <Route path="https://yaswanth3100.github.io/tour-app/" exact component={ Destinations} ></Route>
         <Route path="https://yaswanth3100.github.io/tour-app/" exact component={ TopPicks } ></Route>
         <Route path="https://yaswanth3100.github.io/tour-app/places/:name" exact component={ Visit } ></Route>
         <Route path="https://yaswanth3100.github.io/tour-app/" exact component={ Contact } ></Route>
          <Route path="https://yaswanth3100.github.io/tour-app/" exact component={ Footer } ></Route>
        </Router>
    </div>
    
  );
}

export default App;
