import React from 'react'
import destinationsData from '../Assets/destinationData'
import {Link} from 'react-router-dom';
const Destinations = () => {
  return (
    <div className="dest__main">
     <center><h1>Our Destinations</h1></center>
      <div className="show__places">
       {
        destinationsData.map((place)=>{
         const {img,city,id}=place;
         return(
          <div key={id}>
           <Link to={`/places/${city}`}>
           <img id="dest__img" src={img} alt=""/>
           </Link>
           <h3>{city}</h3>
          </div>
         )
        })
       }
      </div>
    </div>
  )
}

export default Destinations
